/* my style */

a {
  text-decoration: none;
}

body {
  color: #333333;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  margin: 0;
  background: #ffffff;
}

.container {
  width: 1140px;
  margin: 0 auto;
  max-width: calc(100% - 60px);
  margin: 0 auto;
}

#jGrowl {
  z-index: 999999;
}

*:focus {
  outline: none;
}

.hide {
  display: none;
}

a {
  color: #455bd7;
}

.btn:hover {
