}

header {
  background-color: #ffffff;
  width: 100%;
  position: fixed;
  z-index: 100;
  transition: .5s;
}

header.header_with-box-shadow {
  box-shadow: 0 0 30px rgba(9, 32, 61, 0.2);
}

.padding-for-fixed-header {
