}

.page-header {
  width: 1140px;
  max-width: calc(100% - 60px);
  margin: 0 auto;
  /*****************************/
}

.page-header__top-line {
  display: flex;
  align-items: center;
  padding: 32px 0 44px;
}

.page-header__burger {
  display: none;
}

.page-header__logo {
  flex-grow: 1;
}

.page-header__logo img {
  width: 278px;
}

.page-header__logo-img-mob {
  display: none;
}

.page-header__search {
  margin-right: 4.6%;
  padding-bottom: 9px;
  position: relative;
}

.page-header__search input {
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  border: none;
  border-bottom: 1px solid #cccccc;
  width: 206px;
  padding: 0 30px 0 3px;
}

.page-header__search input::placeholder {
  color: #666666;
}

.page-header__search-btn {
  position: absolute;
  right: 10px;
  top: 5px;
  cursor: pointer;
}

.page-header__phone-numbers {
  margin-right: 3%;
  text-align: right;
  position: relative;
}

.page-header__main-phone {
  font-size: 22px;
  font-weight: 500;
  line-height: 26.65px;
  text-transform: uppercase;
  color: #333333;
  position: relative;
}

.page-header__main-phone::after {
  content: '';
  position: absolute;
  width: 8px;
  height: 9px;
  background: url(../img/arrow-right.svg) no-repeat center center;
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
  right: -21px;
  top: 8px;
  transition-duration: .2s;
}

.page-header__phone-numbers-visible {
  padding-right: 31px;
  cursor: pointer;
}

.page-header__phone-numbers-visible.another-visible .page-header__main-phone::after {
  transform: rotate(270deg);
}

.page-header__phones-desc {
  font-size: 14px;
  color: #666666;
  font-weight: 400;
  line-height: 26.65px;
}

.page-header__another-phones {
  display: none;
  position: absolute;
  box-shadow: -7px 19px 50px rgba(9, 32, 61, 0.2);
  border-radius: 15px;
  background-color: #ffffff;
  padding: 35px 39px 42px;
  text-align: left;
}

.page-header__another-phones .city {
  white-space: nowrap;
  font-weight: 700;
  line-height: 38px;
}

.page-header__another-phones .number {
  white-space: nowrap;
  line-height: 16px;
}

.page-header__online-req-btn {
  padding: 0 40px;
  border-radius: 30px;
  background-color: #ea961c;
  font-weight: 700;
  line-height: 26.65px;
  color: #ffffff;
  text-align: center;
  line-height: 60px;
  display: block;
}

.page-header__bottom-line {
  display: flex;
  align-items: center;
  font-weight: 700;
  line-height: 26.65px;
  justify-content: space-between;
  padding-bottom: 20px;
}

.page-header__menu ul {
  display: flex;
  list-style: none;
  margin: 0 -16px;
}

.page-header__menu li {
  margin: 0 16px;
}

.page-header__menu a {
  color: #333333;
}

.page-header__menu a.active {
  position: relative;
  color: #455bd7;
}

.page-header__menu a.active::after {
  content: '';
  position: absolute;
  width: 100%;
  left: 0;
  bottom: -24px;
  height: 3px;
  border-radius: 2px;
  background-color: #455bd7;
}

.page-header__menu a:hover {
  color: #455bd7;
}

.page-header__lang ul {
  display: flex;
  list-style: none;
  margin: 0 -14px;
  line-height: 16px;
}

.page-header__lang li {
  border-right: 1px solid #c3c3c3;
  padding: 0 14px;
  transition: 0.2s;
}

.page-header__lang li:last-child {
