#online-req-modal {
  display: none;
  border-radius: 15px;
  cursor: unset;
}

#online-req-modal .fancybox-close-small {
  width: 15px;
  height: 15px;
  background: url(../img/close.svg) no-repeat center center;
  background-size: 100% 100%;
  right: 30px;
  top: 30px;
  padding: 0;
}

#online-req-modal .fancybox-close-small svg {
  display: none;
}

#online-req-modal .title {
  font-size: 30px;
  line-height: 60px;
  font-weight: 400;
  margin-top: 10px;
}

#online-req-modal .input-line .desc {
  font-size: 14px;
  line-height: 36px;
  font-weight: 700;
}

#online-req-modal .input-line .star-orange {
  color: #ea961c;
}

#online-req-modal .input-line input,
#online-req-modal .input-line .message {
  border-radius: 6px;
  background-color: #f6f6f6;
  border: 0;
  font-family: Montserrat;
  color: #333;
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
}

#online-req-modal .input-line input {
  height: 50px;
  padding: 0 15px;
  line-height: 50px;
  width: 100%;
}

#online-req-modal .input-line .message {
  padding: 15px;
  width: 100%;
  height: 130px;
  resize: none;
}

#online-req-modal .agreement {
  margin-top: 20px;
  position: relative;
}

#online-req-modal .agreement #agr-ch-req {
  display: none;
}

#online-req-modal .agreement #agr-ch-req:checked + .agr-check::before {
  opacity: 1;
}

#online-req-modal .agreement .agr-check {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0;
  cursor: pointer;
  width: 23px;
  height: 22px;
  border-radius: 4px;
  background-color: #ea961c;
}

#online-req-modal .agreement .agr-check::before {
  transition-duration: .2s;
  opacity: 0;
  content: '';
  background: url("../img/checkbox-arrow.png") no-repeat center center;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
}

#online-req-modal .agreement p {
  padding-top: 3px;
  padding-left: 33px;
}

#online-req-modal .agreement a {
  text-decoration: underline;
}

#online-req-modal .agreement a:hover {
  text-decoration: none;
}

#online-req-modal .submit {
  display: flex;
  justify-content: center;
  width: 100%;
}

#online-req-modal .submit .btn-orange,
#online-req-modal .submit .btn-blue {
  margin: 0 auto;
  margin: 30px auto 0;
}

