.btn-orange,
.btn-blue,
.btn-orange .button__bg,
.btn-blue .button__bg {
  -webkit-font-smoothing: antialiased;
  border: none;
  display: inline-block;
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
  user-select: none;
  border-radius: 30px;
  padding: 20px 40px 21px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.2;
}

.btn-orange:focus,
.btn-blue:focus,
.btn-orange .button__bg:focus,
.btn-blue .button__bg:focus {
  outline: none;
}

.svg-filters {
  position: absolute;
  visibility: hidden;
  width: 1px;
  height: 1px;
}

.btn-orange,
.btn-blue {
  color: #ffffff;
  background: none;
  position: relative;
  z-index: 0;
  -webkit-filter: none !important;
  filter: none !important;
}

.safari .btn-orange .button__bg,
.safari .btn-blue .button__bg {
  -webkit-filter: none !important;
  filter: none !important;
}

.btn-orange .button__container,
.btn-blue .button__container {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  z-index: -1;
  transform: translateZ(0);
  transition: all 0.1s ease-out;
}

.btn-orange .button__bg,
.btn-blue .button__bg,
.btn-orange .circle,
.btn-blue .circle {
  background: #ea961c;
  animation: hue-rotate 10s linear infinite;
}

.btn-orange .button__bg,
.btn-blue .button__bg {
  background: #ea961c;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  box-sizing: border-box;
}

.btn-orange .circle,
.btn-blue .circle {
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 15px;
  background: #ea961c;
}

.btn-orange .circle.top-left,
.btn-blue .circle.top-left {
  top: 10px;
  left: 10px;
}

.btn-orange .circle.bottom-right,
.btn-blue .circle.bottom-right {
  bottom: 10px;
  right: 10px;
}

.btn-blue .button__bg,
.btn-blue .circle {
  background: #455bd7;
}

.btn-blue .button__bg {
  background: #455bd7;
}

.btn-blue .circle {
  background: #455bd7;
}

