}

.ul,
.text-style ul {
  list-style: none;
}

.ul li,
.text-style ul li {
  padding-left: 29px;
  font-size: 16px;
  line-height: 26px;
  position: relative;
}

.ul li::before,
.text-style ul li::before {
  content: "";
  display: block;
  width: 5px;
  height: 5px;
  border-radius: 100%;
  background-color: #455bd7;
  position: absolute;
  left: 0;
  top: 11px;
}

.ol,
.text-style ol {
  list-style: none;
  counter-reset: my-counter;
}

.ol li,
.text-style ol li {
  padding-left: 29px;
  font-size: 16px;
  line-height: 26px;
  position: relative;
  counter-increment: my-counter;
}

.ol li::before,
.text-style ol li::before {
  content: counter(my-counter) ".";
  display: block;
  position: absolute;
  left: 0;
  top: 6px;
  color: #455bd7;
  font-size: 16px;
  font-weight: 700;
  line-height: 1em;
}

.pagetitle {
  margin-top: 10px;
  margin-bottom: 55px;
}

.text-style h1:not(:last-child),
.text-style h2:not(:last-child),
.text-style h3:not(:last-child),
.text-style h4:not(:last-child) {
