.input {
  color: #333333;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 400;
  padding: 14px 22px 15px;
  box-sizing: border-box;
  width: 303px;
  border-radius: 6px;
  background-color: #f7f8f9;
  border: 1px solid #f7f8f9;
  margin: 0;
}

.input::placeholder {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #333333;
  opacity: 0.7;
}

.input.not-empty,
.input:focus {
  outline: none;
  border-color: #455bd7;
  background: #eceffb;
}

.input.error {
  border-color: #ea961c;
  background: #fdf4e8;
}

.inputfile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.inputfile + label {
  color: #333333;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: 400;
  background-color: #f7f8f9;
  border: 1px solid #f7f8f9;
  display: inline-block;
  cursor: pointer;
  margin: 0;
  padding: 14px 54px 15px 22px;
  width: 303px;
  box-sizing: border-box;
  border-radius: 6px;
  position: relative;
}

.inputfile + label * {
  pointer-events: none;
}

.inputfile + label:hover {
  border-color: #455bd7;
  background: #eceffb;
}

.inputfile + label:hover::after {
  background-image: url("../img/input-file-select.svg");
}

.inputfile + label::after {
  content: "";
  display: inline-block;
  width: 21px;
  height: 21px;
  background: url("../img/input-file.svg") center center no-repeat;
  background-size: contain;
  position: absolute;
  right: 19px;
  top: calc(50% - 10px);
}

.inputfile.has-file + label,
.inputfile:focus + label {
  border-color: #455bd7;
  background: #eceffb;
}

.inputfile.has-file + label::after,
.inputfile:focus + label::after {
  background-image: url("../img/input-file-select.svg");
}

.checkbox input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 0;
}

.checkbox input + span {
  position: relative;
  padding: 0 0 0 26px;
  cursor: pointer;
}

.checkbox input + span::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 22px;
  height: 22px;
  border-radius: 4px;
  border: 2px solid #ea961c;
  background-color: #ffffff;
  box-sizing: border-box;
}

.checkbox input + span::after {
  content: "";
  display: none;
  position: absolute;
  left: 9px;
  top: 5px;
  width: 6px;
  height: 9px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
  box-sizing: border-box;
}

.checkbox input:checked + span::before {
  background: #ea961c;
}

.checkbox input:checked + span::after {
  display: block;
}

.checkbox input:not(:checked):focus + span:after,
.checkbox input:not(:checked):hover + span:after {
  display: block;
  border-color: #ea961c;
}

.close-btn {
  display: inline-block;
  width: 41px;
  height: 41px;
  border-radius: 100%;
  padding: 0;
  margin: 0;
  border: none;
  cursor: pointer;
  background: transparent url("../img/close.svg") center center no-repeat;
  background-size: 15px;
}

.close-btn:hover {
  background-color: #ea961c;
  background-image: url("../img/close-hover.svg");
}

.circle-btn {
  cursor: pointer;
  padding: 0;
  margin: 0;
  display: inline-block;
  width: 50px;
  height: 50px;
  box-sizing: border-box;
  border-radius: 100%;
  border: 2px solid #d6d6d6;
  background-color: transparent;
  background-size: 13px 13px;
  background-repeat: no-repeat;
  background-position: center center;
}

.circle-btn.arr-left {
  background-image: url("../img/arr-left.svg");
}

.circle-btn.arr-right {
  background-image: url("../img/arr-right.svg");
}

.circle-btn.arr-up {
  background-image: url("../img/arr-up.svg");
}

.circle-btn.arr-down {
  background-image: url("../img/arr-down.svg");
}

.circle-btn.zoom {
  background-image: url("../img/zoom.svg");
  transform: rotate(90deg);
  background-size: 17px 17px;
}

.circle-btn:hover {
  border-color: #ea961c;
  background-color: #ea961c;
}

.circle-btn:hover.arr-left {
  background-image: url("../img/arr-left-hover.svg");
}

.circle-btn:hover.arr-right {
  background-image: url("../img/arr-right-hover.svg");
}

.circle-btn:hover.arr-up {
  background-image: url("../img/arr-up-hover.svg");
}

.circle-btn:hover.arr-down {
  background-image: url("../img/arr-down-hover.svg");
}

.circle-btn:hover.zoom {
  background-image: url("../img/zoom-hover.svg");
}

.circle-btn:after {
  display: none;
}

